.center-div {
    height: 99vh;
    overflow-y: auto;
    padding: 20px;
}
.center-div::-webkit-scrollbar {
    background-color: var(--background);
}
.center-div::-webkit-scrollbar-thumb {
	background-color: var(--background);
}

.user-info {
    background-color: var(--background);
    border: solid 1px var(--border);
    width: 500px;
    height: 180px;
    position: relative;
    border-radius: 2%;
    padding: 10px;
    
    .user-info-left {
        #avatar {
            width: 112px;
            height: 112px; 
            object-fit: cover;
            border-radius: 50%;
        }
    
        #name {
            color: var(--text);
            margin-top: 16px;
            font-size: 18px;
        }
    }
    
    .user-info-right {
        padding: 10px;

        #github-link {
            height: 20px;
            color: var(--text);
            font-size: 14px;
            color: #E5E5E5;
            text-decoration: none;
            text-align: right;
    
            #username {
                font-size: 14px;
                margin: 0px;
            }
        }
        #github-link:hover {
            cursor: pointer;
            text-shadow: 0 0 5px #E5E5E5;
        }

        .user-stats {
            color: var(--text);

            .user-stats-item {
                display: flex;
                flex-direction: row;
                justify-content: end;
                gap: 6px;

                p {
                    font-size: 14px;
                    color: var(--secondary-text);
                    margin: 0;
                }

                h2 {
                    font-size: 24px;
                    margin: 0;
                }
            }
        }
    }  
}

table {
    width: 660px;
    margin-top: 30px;
    border: 0.25px solid var(--border);

    th, td {
        color: var(--text);
        border: 0.25px solid var(--border);
    }
    tr {
        border: 0.25px solid var(--border);
        min-height: 75px;
    }
    th {
        text-align: center;
        padding: 16px;
    }
    td {
        padding: 20px 16px;
        min-height: 100%;
        font-size: 14px;
    }

    .repo-name {
        min-width: 100px;
    }
    .repo-desc {
        color: var(--secondary-text);
        width: 305px;
    }
    .repo-btn {
        height: 100%;
        display: grid;
        place-items: center;
        text-align: center;
    }

    .button {
        display: grid;
        place-items: center;
        text-align: center;
        width: 60px;
        min-height: 30px;
        border: 1px solid var(--secondary-text);
        background-color: var(--border);
        color: var(--text);
        text-decoration: none;
        border-radius: 5%;
    }
    .button:hover {
        color: var(--accent);
    }
    .button:hover, .button:focus {
        box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
        transform: translateY(-0.25em);
    }
    // td:has(.button) { 
    //     display: grid;
    //     place-content: center;
    //     border: none;
    //     border: 1px solid pink;
    //     height: 100%;
    // }    messes up the td with button inside
}

.pagination-area {
    display: grid;
    place-items: center;
    padding: 20px;

    .page-numbers {
        color: var(--secondary-text);
        font-size: 14px;
    }

    .page-buttons {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 8px;

        button:hover, button:focus {
            box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
            transform: translateY(-0.25em);
        }
        button {
            text-align: center;
            width: 80px;
            min-height: 30px;
            border: 1px solid var(--secondary-text);
            background-color: var(--border);
            color: var(--text);
            text-decoration: none;
            border-radius: 5%;
        }
        button:hover {
            color: var(--accent);
        }
        button:disabled {
            color: var(--background);
            background-color: var(--secondary-text);
            transform: none;
        }
    }

    .input-num {
        background-color: var(--background);
        width: 50px;
        border: solid 0.25px var(--border);
        box-sizing: border-box;
    }
    .input-num[type=number] {
        color: var(--secondary-text);
    }
    .input-num:focus {
        outline: none;
    }
    
    .page-sizing {
        background-color: var(--background);
        margin: 18px;
        color: var(--secondary-text);
        width: 100px;
        border: solid 0.25px var(--border);
        box-sizing: border-box;
    }
    .page-sizing:focus {
        outline: none;
    }
}




